.button,.create__button{display:block;background:#287bbb;color:#fff;border:0;padding:8px 20px 8px;font-size:12px;font-weight:600;text-transform:uppercase;cursor:pointer;border-radius:5px;padding-top:18px;padding-bottom:15px;text-decoration:none;width:100%;outline:none}.button:disabled,.create__button:disabled{background:#dddddd;cursor:default}.create{position:relative;width:100%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.create__button--opened{border-radius:0 5px 5px 0;width:30%}.create__input{width:75%}.create>*{-webkit-transition:width .25s;transition:width .25s}.create>*.v-enter,.create>*.v-leave{width:0}



